import React from 'react';
import { graphql, navigate } from 'gatsby';
// import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import CreateIcon from '@mui/icons-material/Create';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import Layout from '../components/layout';
import Bio from '../components/bio';
import Link from '../components/link';

const BlogPostTemplate = ({ data, location }) => {
  const { previous, next, markdownRemark: post } = data;
  const thumbnailUrl =
    post.frontmatter.thumbnail?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;

  const Afs = () => (
    <Box
      sx={{
        display: 'inline-flex',
        width: '100%',
        overflow: 'scroll'
      }}
    >
      {post.frontmatter.afs?.map((__html, i) => (
        <Box key={i} dangerouslySetInnerHTML={{ __html }} mr={2} />
      ))}
    </Box>
  );

  return (
    <Layout
      location={location}
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
      thumbnail={thumbnailUrl}
    >
      <Box component='article' itemScope itemType='http://schema.org/Article'>
        <Box component='header' mb={5}>
          <Typography itemProp='headline' variant='h1'>
            {post.frontmatter.title}
          </Typography>
          <Box display='flex' alignItems='center' my={1}>
            <CreateIcon
              sx={{
                fontSize: 16
              }}
            />
            <Typography variant='body2'>{post.frontmatter.date}</Typography>
          </Box>
          <Stack
            direction='row'
            spacing={1}
            my={1}
            sx={{
              overflow: 'scroll'
            }}
          >
            {post.frontmatter?.tags?.map(label => (
              <Chip
                key={label}
                label={label}
                onClick={() => navigate(`/tags/${label}/`)}
                icon={
                  <LocalOfferIcon
                    sx={{
                      fontSize: 16
                    }}
                  />
                }
              />
            ))}
          </Stack>
        </Box>
        <Afs />
        <Box
          component='section'
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp='articleBody'
        />
        <Afs />
        <Divider
          sx={{
            my: 5
          }}
        />
        <Box component='footer'>
          <Bio />
        </Box>
      </Box>
      <Box component='nav' className='blog-post-nav'>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0
          }}
        >
          <li>
            {previous && (
              <Link
                href={`/articles${previous.fields.slug}`}
                rel='prev'
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                <ArrowBackIosIcon /> {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link
                href={`/articles${next.fields.slug}`}
                rel='next'
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-end'
                }}
              >
                {next.frontmatter.title} <ArrowForwardIosIcon />
              </Link>
            )}
          </li>
        </ul>
      </Box>
    </Layout>
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(truncate: true, pruneLength: 140)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
        thumbnail {
          childImageSharp {
            gatsbyImageData(width: 200)
          }
        }
        afs
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`;
